<template>
  <section>

    <!---->
    <el-breadcrumb separator="/" class="bread-nav">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-form :inline="true">
        <el-col :span="4">
          <el-form-item>
            <el-input placeholder="" v-model="add_user.username"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item>
            <el-input placeholder="" v-model="add_user.account_number"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item>
            <el-input placeholder="" v-model="add_user.password"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="3">
          <el-form-item>
            <el-select v-model="add_user.role" placeholder="">
              <el-option v-for="item in add_user.roles" :key="item.value" :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item>
            <el-button type="primary" @click="addUser"></el-button>
          </el-form-item>
        </el-col>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="bundle_table_data" style="width: 100%">
      <el-table-column label="ID" v-if="config.id_show">
        <template scope="scope">
          <el-tag>{{ scope.row.user.id }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column type="index" width="100"></el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tag type="primary">{{ scope.row.user.userName }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span>{{ scope.row.user.loginName }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span><el-tag type="danger">{{ scope.row.roleList[0].roleName }}</el-tag></span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button size="small"
                     @click="handleEdit(scope.row.user.id,scope.row.user.loginName,scope.row.user.userName,scope.row.roleList[0].roleName,scope.row.user.loginName)">
            
          </el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.row.user.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="config.dialogVisible" size="tiny" class="model">
      <el-input v-model="edit.user_name">
        <template slot="prepend"></template>
      </el-input>
      <br><br>
      <el-input v-model="edit.login_name">
        <template slot="prepend">&nbsp;&nbsp;&nbsp;</template>
      </el-input>
      <br><br>
      <el-input placeholder="" v-model="edit.password">
        <template slot="prepend">&nbsp;&nbsp;&nbsp;</template>
      </el-input>
      <br><br>
      <el-select v-model="edit.update_role" placeholder="" style="display: block">
        <el-option v-for="item in edit.update_roles" :key="item.value" :value="item.value"></el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button @click="config.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitEdit"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <el-pagination layout="prev, pager, next" :total="page.total" :page-size="15" @current-change="handleCurrentChange" style="float: right;margin-top: 20px"></el-pagination>

  </section>
</template>

<script>
  import axios from 'axios';
  let qs = require("qs");

  export default {
    data() {
      return {
        config: {
          // id
          id_show: false,
          dialogVisible: false,
        },
        // 
        add_user: {
          userName: '',
          account_number: '',
          password: '',
          role: '',
          roles: [
            {value: ''},
            {value: ''},
            {value: ''}
          ]
        },
        // 
        edit: {
          id: '',
          user_name: '',
          login_name: '',
          current_login_name: '',
          password: '',
          update_role: '',
          update_roles: [
            {value: ''},
            {value: ''},
            {value: ''}
          ]
        },
        // 
        bundle_table_data: [],
        //
        page:{
            total:80,
            tagret_page:1
        }
      }
    },
    methods: {
      // 
      getUserData() {
        let self = this;
        axios.post('user_queryAllUsers.ajax',qs.stringify({
          startPage:self.page.tagret_page
        })
        )
          .then(function (res) {
            self.bundle_table_data = res.data.list;
            self.page.total = res.data.page.totalCount;
          });
      },
      // 
      addUser() {
        let self = this;
        let data_role;
        switch (self.add_user.role) {
          case "":
            data_role = 1;
            break;
          case "":
            data_role = 2;
            break;
          case "":
            data_role = 3;
            break;
        }
        axios.post('user_addUser.ajax', qs.stringify({
            userName: self.add_user.username,
            roleIds: data_role,
            loginName: self.add_user.account_number,
            password: self.add_user.password
          })
        )
          .then(function (res) {
            if (res.data.data === true) {
              self.$message("");
              self.getUserData();
            } else {
              self.$message({
                showClose: true,
                message: res.data.msg,
                type: 'warning'
              });
            }
          });
      },
      // 
      handleDelete(id) {
        let self = this;
        axios.post('user_deleteUserByIds.ajax', qs.stringify({
            ids: id
          })
        )
          .then(function (res) {
            self.$message("");
            self.getUserData();
          });
      },
      // 
      handleEdit(id, login_name, user_name, role, current_user) {
        this.config.dialogVisible = true;
        this.edit.id = id;
        this.edit.login_name = login_name;
        this.edit.user_name = user_name;
        this.edit.update_role = role;
        this.edit.password = '';
        this.edit.current_login_name = current_user;
      },
      // 
      submitEdit() {
        let self = this;
        let data_update_role;
        switch (self.edit.update_role) {
          case "":
            data_update_role = 1;
            break;
          case "":
            data_update_role = 2;
            break;
          case "":
            data_update_role = 3;
            break;
        }
        axios.post('user_updateUser.ajax', qs.stringify({
            id: self.edit.id,
            loginName: self.edit.login_name,
            userName: self.edit.user_name,
            password: self.edit.password,
            roleIds: data_update_role,
            currentLoginName: self.edit.current_login_name
          })
        )
          .then(function (res) {
            if (res.data.data == true) {
              self.config.dialogVisible = false;
              self.$message("");
              self.getUserData();
            } else {
              self.$message({
                showClose: true,
                message: res.data.msg,
                type: 'warning'
              });
            }
          });
      },
      // 
      handleCurrentChange(page){
          this.page.tagret_page = page;
          this.getUserData();
      }
    },
    mounted: function () {
      this.getUserData();
    }
  }
</script>

<style scoped>
  * {
    margin: 0;
  }

  .bread-nav {
    float: right;
    margin-bottom: 10px;
  }

  .toolbar {
    background: #f2f2f2;
    margin: 5px 0;
  }

  .toolbar .el-form-item {
    margin: 20px 0 5px 20px;
  }

</style>



