<template>
  <section>

    <!---->
    <el-breadcrumb separator="/" class="bread-nav">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-form :inline="true">
        <el-col :span="7">
          <el-form-item label="">
            <el-select v-model="query_bundle.query_type" placeholder="">
              <el-option v-for="item in query_bundle.all_type" :key="item.id" :value="item.name + item.num"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="7">
          <el-form-item label="">
            <el-input placeholder="" v-model="query_bundle.query_num"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item>
            <el-button type="primary" icon="search" @click="getQueryBundleData"></el-button>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item>
            <el-button type="success" @click="addBundle"></el-button>
          </el-form-item>
        </el-col>
      </el-form>
    </el-col>

    <!---->
    <el-dialog title="" :visible.sync="config.add_bundle_visible" size="tiny">
      <el-form label-width="80px"><br>
        <el-form-item label="">
          <el-select v-model="add_bundle.add_type" placeholder="">
            <el-option v-for="item in add_bundle.all_type" :key="item.id" :value="item.name + item.num"></el-option>
          </el-select>
        </el-form-item>
        <br>
        <el-form-item label="">
          <el-input v-model="add_bundle.add_num"></el-input>
        </el-form-item>
        <br>
        <el-form-item label="">
          <el-input v-model="add_bundle.add_slateName"></el-input>
        </el-form-item>
        <br>
        <el-form-item label="">
          <el-input v-model="add_bundle.add_price"></el-input>
        </el-form-item>
        <br>
        <el-form-item label="">
          <el-input v-model="add_bundle.add_description"></el-input>
        </el-form-item>
      </el-form>
      <br>
      <el-form style="text-align: center" class="add_bundle_rows">
        <el-row>
          <el-button type="primary" @click="addBundleRow"></el-button>
        </el-row>
        <br>
        <el-row class="add_bundle_row">
          <el-col :span="5">
            <el-input class="add_bundle_width"></el-input>
          </el-col>
          <el-col :span="4">*</el-col>
          <el-col :span="5">
            <el-input class="add_bundle_height"></el-input>
          </el-col>
          <el-col :span="4"></el-col>
          <el-col :span="5">
            <el-input class="add_bundle_num"></el-input>
          </el-col>
        </el-row>
        <br/>
        <el-row class="add_bundle_row">
          <el-col :span="5">
            <el-input class="add_bundle_width"></el-input>
          </el-col>
          <el-col :span="4">*</el-col>
          <el-col :span="5">
            <el-input class="add_bundle_height"></el-input>
          </el-col>
          <el-col :span="4"></el-col>
          <el-col :span="5">
            <el-input class="add_bundle_num"></el-input>
          </el-col>
        </el-row>
        <br/>
      </el-form>
      <br>
      <span slot="footer" class="dialog-footer">
        <el-button @click="config.add_bundle_visible = false"> </el-button>
        <el-button type="primary" @click="submitAddBundle"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <el-table :data="bundle_table_data" style="width: 100%">
      <el-table-column type="index"></el-table-column>
      <el-table-column label="ID" v-if="config.id_show">
        <template scope="scope">
          <el-tag>{{ scope.row.id }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tag type="danger">{{ scope.row.kind.name }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tag type="primary">{{ scope.row.num }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span>{{ scope.row.in_time }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span>{{ scope.row.out_time }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span><el-tag type="danger">{{ scope.row.originalCount }}</el-tag></span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span>{{ scope.row.originalAcreage }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span>{{ scope.row.outCount }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span>{{ scope.row.outAcreage }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span>{{ scope.row.backCount }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span>{{ scope.row.currentCount }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span>{{ scope.row.currentAcreage }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span>{{ scope.row.description }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button size="small" type="primary" @click="outputBundle(scope.row.id)">
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="config.output_bundle_visible">
      <el-table :data="slate_table_data" ref="multipleTable" style="width: 100%" @selection-change="selectOutput">
        <el-table-column type="selection">
        </el-table-column>
        <el-table-column label="ID">
          <template scope="scope">
            <el-tag type="primary">{{ scope.row.id }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-tag type="danger">{{ scope.row.slateName }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <span>{{ scope.row.price }}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-tag>{{ scope.row.length }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-tag>{{ scope.row.height }}</el-tag>
          </template>
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="config.output_bundle_visible = false"> </el-button>
        <el-button type="primary" @click="submitOutputBundle"> </el-button>
      </span>
    </el-dialog>

  </section>
</template>

<script>
  import axios from 'axios';
  import $ from 'jquery';
  let qs = require("qs");

  export default {
    data() {
      return {
        config: {
          id_show: false,
          add_bundle_visible: false,
          output_bundle_visible: false
        },
        // 
        query_bundle: {
          all_type: [],
          query_num: '',
          query_type: ''
        },
        // 
        bundle_table_data: [],
        // 
        add_bundle: {
          add_type: '',
          add_num: '',
          add_slateName: '',
          add_price: '',
          add_description: ''
        },
        // 
        slate_table_data: [],
        multipleSelection: [],
        // 
        selects: [],
        bundle_code: ''
      }
    },
    methods: {
      //
      getStoneType(){
        let self = this;
        axios.post('kind_queryAllKind.ajax')
          .then(function (res) {
            self.query_bundle.all_type = res.data.list;
            self.add_bundle.all_type = res.data.list;
            let list = res.data.list;
            for (let i = 0; i < list.length; i++) {
              sessionStorage.setItem(list[i].name + list[i].num, list[i].id);
            }
          });
      },
      // 
      getBundleData(){
        let self = this;
        axios.post('stabKindAndSlate_queryStabKindByPage.ajax')
          .then(function (res) {
            self.bundle_table_data = res.data.list;
          });
      },
      // 
      getQueryBundleData(){
        let self = this;
        let query_id;
        if (self.query_bundle.query_type == '' && self.query_bundle.query_num == '') {
          self.$message({
            message: "",
            type: 'warning',
            duration: '1000'
          });
          return;
        }
        for (let i = 0; i < self.query_bundle.all_type.length; i++) {
          if (self.query_bundle.all_type[i].name == self.query_bundle.query_type) {
            query_id = self.query_bundle.all_type[i].id;
            break;
          }
        }
        axios.post('stabKindAndSlate_queryStabKindByKindIdOrNum.ajax', qs.stringify({
            num: self.query_bundle.query_num,
            id: query_id
          })
        )
          .then(function (res) {
            self.bundle_table_data = res.data.list;
          });
      },
      // 
      addBundle() {
        this.config.add_bundle_visible = true;
      },
      // 
      addBundleRow(){
        $('<div class="el-row" class="add_bundle_row">' +
          '<div class="el-col el-col-5"><div class="el-input"><input autocomplete="off" type="text" rows="2" validateevent="true" class="el-input__inner add_bundle_width"></div></div>' +
          '<div class="el-col el-col-4">*</div>' +
          '<div class="el-col el-col-5"><div class="el-input"><input autocomplete="off" type="text" rows="2" validateevent="true" class="el-input__inner add_bundle_height"></div></div>' +
          '<div class="el-col el-col-4"></div>' +
          '<div class="el-col el-col-5"><div class="el-input"><input autocomplete="off" type="text" rows="2" validateevent="true" class="el-input__inner add_bundle_num"></div></div>' +
          '</div><br/>').appendTo(".add_bundle_rows");
      },
      // 
      submitAddBundle(){
        let self = this;
        let kind_id = sessionStorage.getItem(self.add_bundle.add_type),
          data = [];
        $(".add_bundle_row").each(function () {
          let num = $(this).find(".add_bundle_num > input").val(),
            width = $(this).find(".add_bundle_width > input").val(),
            height = $(this).find(".add_bundle_height > input").val();
          if (num) {
            for (let i = 0; i < num; i++) {
              data.push({
                length: width,
                height: height
              });
            }
          }
        });
        axios.post('stabKindAndSlate_addStabKind.ajax', {
          kindId: kind_id,
          num: self.add_bundle.add_num,
          slateName: self.add_bundle.add_slateName,
          price: self.add_bundle.add_price,
          description: self.add_bundle.add_description,
          data: data
        })
          .then(function (res) {
            if (res.data.data == true) {
              self.getBundleData();
              self.config.add_bundle_visible = false;
              self.$message({
                message: res.data.msg,
                type: 'info',
                duration: 800
              });
            } else {
              self.$message(res.data.msg);
            }
          })
      },
      // 
      outputBundle(id){
        let self = this;
        self.bundle_code = id;
        self.config.output_bundle_visible = true;
        axios.post('stabKindAndSlate_querySlateByStabKindId.ajax', qs.stringify({
            stabKindId: id
          })
        )
          .then(function (res) {
            if (res.data.data == true) {
              self.slate_table_data = res.data.list;
            }
          });
      },
      // 
      selectOutput(selects){
        this.selects = selects;
      },
      // 
      submitOutputBundle(){
        let self = this,
          ids = self.selects.map(item => item.id).toString();
        axios.post('stabKindAndSlate_outStock.ajax', qs.stringify({
            ids: ids,
            stabKindId: self.bundle_code
          })
        )
          .then(function (res) {
            if (res.data.data == true) {
              self.$message({
                message: "",
                type: 'success',
                duration: '1000'
              });
              self.getBundleData();
              self.config.output_bundle_visible = false;
            }
          });
      }
    },
    mounted: function () {
      this.getBundleData();
      this.getStoneType();
    }
  }
</script>

<style scoped>
  * {
    margin: 0;
  }

  .bread-nav {
    float: right;
    margin-bottom: 10px;
  }

  .toolbar {
    background: #f2f2f2;
    margin: 5px 0;
  }

  .toolbar .el-form-item {
    margin: 20px 0 5px 20px;
  }
</style>



