<template>
  <section>

    <!---->
    <el-breadcrumb separator="/" class="bread-nav">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-form :inline="true">
        <el-col :span="4">
          <el-form-item>
            <el-input placeholder="" v-model="add_custom.name"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item>
            <el-input placeholder="" v-model="add_custom.phone"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item>
            <el-button type="primary" @click="addCustom"></el-button>
          </el-form-item>
        </el-col>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="custom_table_data" style="width: 100%">
      <el-table-column label="ID" v-if="config.id_show">
        <template scope="scope">
          <el-tag>{{ scope.row.id }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column type="index" width="100"></el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tag type="primary">{{ scope.row.name }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tag type="success">{{ scope.row.phone }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button size="small"
                     @click="handleEdit(scope.row.user.id,scope.row.user.loginName,scope.row.user.userName,scope.row.roleList[0].roleName,scope.row.user.loginName)">
            
          </el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="config.dialogVisible" size="tiny" class="model">
      <el-input v-model="edit.user_name">
        <template slot="prepend"></template>
      </el-input>
      <br><br>
      <el-input v-model="edit.login_name">
        <template slot="prepend">&nbsp;&nbsp;&nbsp;</template>
      </el-input>
      <br><br>
      <el-input placeholder="" v-model="edit.password">
        <template slot="prepend">&nbsp;&nbsp;&nbsp;</template>
      </el-input>
      <br><br>
      <el-select v-model="edit.update_role" placeholder="" style="display: block">
        <el-option v-for="item in edit.update_roles" :key="item.value" :value="item.value"></el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button @click="config.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitEdit"> </el-button>
      </span>
    </el-dialog>

  </section>
</template>

<script>
  import axios from 'axios';
  let qs = require("qs");

  export default {
    data() {
      return {
        config: {
          // id
          id_show: false,
          dialogVisible: false,
        },
        // 
        add_custom: {
          name: '',
          phone: ''
        },
        // 
        edit: {},
        // 
        custom_table_data: []
      }
    },
    methods: {
      // 
      getCustomData() {
        let self = this;
        axios.post('customer_queryAllCustomer.ajax')
          .then(function (res) {
            self.custom_table_data = res.data.list;
          });
      },
      // 
      addCustom() {
        let self = this;
        axios.post('customer_addCustomer.ajax', qs.stringify({
            name: self.add_custom.name,
            phone: self.add_custom.phone
          })
        )
          .then(function (res) {
            if (res.data.data === true) {
              self.$message("");
              self.getCustomData();
            } else {
              self.$message({
                showClose: true,
                message: res.data.msg,
                type: 'warning'
              });
            }
          });
      },
      // 
      handleDelete(id) {
        let self = this;
        axios.post('customer_deleteCustomerByIds.ajax', qs.stringify({
            ids: id
          })
        )
          .then(function (res) {
            self.$message("");
            self.getCustomData();
          });
      },
      // 
      handleEdit() {

      },
      // 
      submitEdit() {

      },
    },
    mounted: function () {
      this.getCustomData();
    }
  }
</script>

<style scoped>
  * {
    margin: 0;
  }

  .bread-nav {
    float: right;
    margin-bottom: 10px;
  }

  .toolbar {
    background: #f2f2f2;
    margin: 5px 0;
  }

  .toolbar .el-form-item {
    margin: 20px 0 5px 20px;
  }

</style>



